<template>
  <div class="megbox">
    <div class="top">
      <!-- Emoji      rename-->
      <Emoji ref="emoji" :textConent="rename" />

      <!-- prop -->
      <el-form :inline="true" :model="ruleForm" class="demo-form-inline">
        <el-form-item prop="name" label="" :rules="rules.name">
          <el-input v-model="ruleForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="email" label="" :rules="rules.email">
          <el-input
            v-model="ruleForm.email"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <h2></h2>
    <div class="bottom">
      <div class="block">
        <!-- listMessage.vue  type1 -->
        <div class="timeline" v-for="(v, index) in list" :key="index">
          <p>
            <!--  -->
            <span>{{ v.megs.name }}</span
            >{{ v.megs.createdAt }}
          </p>
          <p class="content">{{ v.megs.content }}</p>

          <!-- id   reindex -->
          <p class="reply" @click="replyMessage(index, v.megs.name)"></p>

          <!--  -->
          <div class="rep" v-for="(r, i) in v.reply" :key="i">
            <svg class="icon inicon" aria-hidden="true">
              <use
                xlink:href="#icon-taidixiongwanoukeai"
                v-if="r.star == 0"
              ></use>
              <use xlink:href="#icon-labixiaoxin-" v-if="r.star == 1"></use>
            </svg>
            <p>
              <span>{{ r.name }}</span>
              {{ r.createdAt }}
            </p>
            <p class="content">{{ r.content }}</p>
            <p class="reply" @click="replyMessage(index, r.name)"></p>
          </div>

          <!--  -->
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-labixiaoxin-" v-if="v.megs.star == 1"></use>
            <use
              xlink:href="#icon-taidixiongwanoukeai"
              v-if="v.megs.star == 0"
            ></use>
          </svg>
          <div class="line"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script> 
// 
import Emoji from "../components/Emoji";
import { addMessage, replyMessage } from "../api/index";
import { log } from 'three';

export default {
  data() {
    return {
      // ruleForm
      ruleForm: {},
      //  
      oneMeg: {
        type: 1,
        star: 0,
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 255,
            message: " 1  255 ",
            trigger: "blur",
          },
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: ["blur", "change"],
          },
        ],
      },
      // content
      content: [],
      // Emoji.vue rename+@rename
      rename: '',
      //  id
      uid: '',
      reindex: 0,
    };
  },
  //      Message.vue  Showart.vue
  props: {
    // Message.vue ,   :megsreply!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    list: {
      type: Array,
      // default: [],
      default: function () {
        return []
      }
    },
    // Message.vue ,  1  
    // ShowArt,id
    type: {
      default: "1",
    },
    // //     data   !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    // reindex: 0,
  },
  methods: {
    // 
    onSubmit() {
      // 
      this.$refs.emoji.referContent();

      // content
      this.content = this.$refs.emoji.content;

      // ,return     
      if (this.content.length === 0) return

      // 1
      this.oneMeg.name = this.ruleForm.name;
      this.oneMeg.email = this.ruleForm.email;

      if (!this.ruleForm.name) return alert('')
      if (!this.ruleForm.email) return alert('')

      // this.contentthis.oneMeg.content
      this.oneMeg.content = this.content[this.content.length - 1];
      this.oneMeg.type = this.type;



      //       this.rename == '',
      if (this.rename == '') {
        addMessage(this.oneMeg)
          .then((res) => {
            if (res) {
              console.log(res);
              if (res.code == "200") {
                this.$message({
                  message: "",
                  type: "success",
                  duration: 1000,
                });
                // 
                this.list.unshift({ megs: this.oneMeg });

              } else {
                this.$message({
                  showClose: true,
                  message: "",
                  type: "error",
                  duration: 1000,
                });

              }
            }
          })
          .catch(function (error) {
            console.log(error);
            this.$message({
              showClose: true,
              message: "",
              type: "error",
              duration: 1000,
            });

          });
        this.clearInfo()
      }
      // rename    rename@+
      else {
        // this.oneMeg.name  + rename
        this.oneMeg.name += this.rename;
        // uid
        this.oneMeg.uid = this.uid;
        console.log("this.oneMeg:", this.oneMeg);
        replyMessage(this.oneMeg)
          .then((res) => {
            if (res) {
              console.log('res', res);
              if (res.code == "200") {
                this.$message({
                  message: "",
                  type: "success",
                  duration: 1000,
                });
                console.log('megBox.vuethis.list.reply', this.list.reply);
                // listreply
                this.list[this.reindex].reply.push(this.oneMeg);
              } else {
                this.$message({
                  showClose: true,
                  message: "",
                  type: "error",
                  duration: 1000,
                });

              }
            }
          })
          .catch(function (error) {
            console.log(error);
            this.$message({
              showClose: true,
              message: "",
              type: "error",
              duration: 1000,
            });

          });
        this.clearInfo()
      }
    },

    //  rename+@rename
    replyMessage(i, name) {
      this.rename = '';

      console.log(':', i);
      //   reindex 
      this.reindex = i;

      let n = name.split("@")[0];
      console.log('n', n);

      //   zs@li
      this.rename = `@${n}`;
      console.log('rename', this.rename);

      this.uid = this.list[i].megs.id;
      console.log('uid', this.uid);
    },

    // ,,,rename
    clearInfo() {
      this.ruleForm.name = ''
      this.ruleForm.email = ''
      this.rename = ''
    }
  },
  components: {
    Emoji,
  },
};


</script>
